@charset "utf-8";
@import "_smart-grid";


.center {
	margin: 0 auto;
	max-width: 1440px;
	margin-top: 160px;
	min-height: 2672px;
	height: 100%;
	background: #ECEDF3;
	border-radius: 50px;


	@include xs(margin-top, 60px);

}

.service__box {
	margin-top: 176px;
	width: 100%;
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);

}

.service__right {

	@include xs(margin-top, 100px);

	img {

		@include xs(width, 100%);
	}
}

.service__left {
	max-width: 490px;
	width: 100%;

	h5 {
		@include blue();
	}

	h2 {
		@include black();
		margin-top: 20px;

		@include xs(font-size, 50px);
		@include xs(line-height, 61px);
	}

	p {
		margin-top: 20px;
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 20px;
		line-height: 32px;
		color: #333333;

	}

	.service__left-box {
		display: flex;
		justify-content: space-between;
		align-items: center;

		@include xs(flex-direction, column);
		@include xs(margin-top, 50px);


		img {
			height: 96px;

		}

		blockquote {

			p {
				font-family: 'Inter';
				font-style: normal;
				font-weight: normal;
				font-size: 16px;
				line-height: 32px;
				color: rgba(51, 51, 51, 0.5);
			}
		}

	}

	button {
		margin-top: 20px;
		width: 138px;
		height: 48px;
		border: 1px solid rgba(116, 162, 214, 0.503736);
		border-radius: 6px;
		box-sizing: border-box;
		background-color: #ecedf3;
		cursor: pointer;

		font-family: 'Inter';
		font-style: normal;
		font-weight: bold;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		text-transform: uppercase;
		color: #74A2D6;

		&:focus,
		&:hover {
			outline: none;
			opacity: 0.8;
		}
	}
}


.our {
	margin-top: 234px;
	width: 100%;
	display: flex;
	flex-direction: column;
}

.our__box {
	//border: 1px solid rgb(219, 16, 192);
	display: flex;
	justify-content: space-between;

	@include xs (flex-direction, column);
}

.our__box-left {


	h5 {
		@include blue();
	}

	h2 {
		@include black();
		margin-top: 20px;

		@include xs(font-size, 50px);
		@include xsl(font-size, 46px);

	}
}

.our__box-right {
	display: flex;
	margin-top: auto;
	margin-bottom: 0px;

	@include xs (margin-top, 50px);


	.slider-arrows {}

	.box-right__arrows {
		cursor: pointer;
		margin-right: 32px;

		.prev {
			margin-right: 8px;

			&:focus,
			&:hover {
				opacity: 0.8;
			}
		}

		.next {
			margin-right: 8px;

			&:focus,
			&:hover {
				opacity: 0.8;
			}
		}
	}

	button {
		width: 150px;
		height: 48px;
		border: 1px solid rgba(116, 162, 214, 0.503736);
		box-sizing: border-box;
		border-radius: 6px;
		background-color: #ecedf3;
		cursor: pointer;

		font-family: 'Inter';
		font-style: normal;
		font-weight: bold;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		text-transform: uppercase;
		color: #74A2D6;

		&:focus,
		&:hover {
			outline: none;
			opacity: 0.8;
		}
	}
}



.our__slider {
	//border: 1px solid rgba(116, 162, 214, 0.503736);
	width: 100%;
	margin-top: 67px;



	.our__slider-item {
		//border: 1px solid rgba(116, 162, 214, 0.503736);

		position: relative;
		width: 575px;
		height: 528px;
		border-radius: 24px;

		@include xs (width, 100%);
		//@include xs (height, 100%);


		h3 {
			position: absolute;
			top: 409px;
			left: 50px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: bold;
			font-size: 40px;
			line-height: 48px;
			color: #FFFFFF;

			@include xs (top, 209px);
		}

		p {
			position: absolute;
			top: 458px;
			left: 50px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(255, 255, 255, 0.502972);

			@include xs (top, 258px);
		}

		.google {
			width: 575px;
			height: 528px;
			border-radius: 24px;
			background-image: url('../img/case-left.png');
			background-repeat: no-repeat;

			@include xs (background-size, 100% 100%);
			@include xs (width, 100%);
			@include xs (height, 328px);
		}

		.trello {
			width: 575px;
			height: 528px;
			border-radius: 24px;
			background-image: url('../img/case-right.png');
			background-repeat: no-repeat;

			@include xs (background-size, 100% 100%);
			@include xs (width, 100%);
			@include xs (height, 328px);
		}
	}
}

.our__banner {
	margin-top: 115px;
	width: 100%;
	display: flex;
	justify-content: space-between;

	@include xs (flex-direction, column);
	@include xs (margin-top, 0px);
	@include xs (width, auto);
	@include xs (margin, -100px auto);

	img {
		@include xs (margin-top, 50px);
		@include xs (width, 100%);
	}
}

.slick-slide {
	display: block;
	margin-right: 100px;

	@include xs (margin-right, 0px);
}


.other {
	margin-top: 182px;
	width: 100%;

	h5 {
		@include blue();
		text-align: center;
	}

	h2 {
		@include black();
		text-align: center;

		@include xs (font-size, 53px);

		@include xsl (font-size, 47px);
	}
}


.other__config {
	margin-top: 17px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	@include xs (margin-bottom, 50px);


	.other__config-box {
		display: flex;
		align-items: center;
		width: 355px;
		margin-top: 50px;

		@include col();
		@include size(4);
		@include size-lg (6);
		@include size-xs (12);

		img {
			height: 72px;
		}

		.text__box {
			//border: 1px solid rgb(219, 16, 192);
			margin-left: 24px;

			h6 {
				margin-top: 50px;
				font-family: 'Inter';
				font-style: normal;
				font-weight: normal;
				font-size: 20px;
				margin: 0;
				color: #333333;

				@include xs (margin-top, 16px);

			}

			p {
				max-width: 270px;
				width: 100%;
				font-family: 'Inter';
				font-style: normal;
				font-weight: normal;
				font-size: 16px;
				line-height: 22px;
				color: rgba(51, 51, 51, 0.5);
			}
		}
	}
}