@charset "utf-8";
@import "_smart-grid";

.terminal {
	width: 100%;
	margin-top: 192px;
	
}

.terminal__top {
	//border: 1px solid rgba(116, 162, 214, 0.503736);
	position: relative;
	//width: 100%;
	// display: flex;
	// justify-content: space-between;
	@include xs (height, 820px);


}

.terminal__slider{

}

.terminal__slider-box {
	//border: 3px solid rgba(140, 235, 63, 0.504);
	width: 100%;
	display: flex;
	justify-content: space-between;

	@include xs (flex-direction, column);
	//@include xs (height, 500px);
}

.terminal__top-left {
	//border: 1px solid rgba(116, 162, 214, 0.503736);
	position: relative;
	//max-width: 504px;
	//width: 100%;

	//@include xs (display, flex);
	//@include xs (flex-direction, column);
	@include xs (height, 900px);



	.mans {
		margin-left: 70px;

		@include xs (margin-left, 0px);
		@include xs (width, 100%);


	}

	.graph {
		position: absolute;
		left: -33px;
		top: 64px;

		@include xs (display, none);
	}
}

.terminal__top-right {
	//border: 1px solid rgba(116, 162, 214, 0.503736);
	max-width: 504px;
	width: 100%;
	position: absolute;
	right: -40px;
	top: 0;


	@include xs (top, 400px);
	@include xs (right, 0px);
	@include xs (text-align, center);


	h5 {
		@include blue();
		@include xs(margin-top, 20px);
	}

	h2 {
		@include black();

		@include xs(font-size, 46px);
		@include xs(line-height, 55px);
		@include xs(margin-top, 20px);

	}

	blockquote {
		margin-left: 0px;

		@include xs(margin, 15px auto);


		p {
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(51, 51, 51, 0.5);

		}
	}
}

.terminal__arrows {
	//border: 1px solid #24aceb;
	position: absolute;
	top: 57%;
	right: 32%;

	@include xs (top, 44%);
	@include xs (right, 0px);
	@include xs (display, flex);
	@include xs (justify-content, space-between);
	@include xs (width, 100%);

	.terminal__prev{
		//position: absolute;

	}

	.terminal__prev,
	.terminal__next {
		cursor: pointer;

		&:focus,
		&:hover {
			opacity: 0.8;
		}
	}
}




//mail
.mail {
	position: relative;
	margin-top: 208px;
	width: 100%;
	height: 320px;
	background: #ECEDF3;
	border-radius: 24px;

	@include xs(height, 350px);


	h2 {
		padding-top: 60px;
		margin-left: 68px;
		@include black();

		@include xs(margin-left, auto);
		@include xs(text-align, center);
	}

	p {
		margin-left: 68px;
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 32px;
		color: #333333;

		@include xs(margin-left, auto);
		@include xs(text-align, center);
	}


	.box__img {
		position: absolute;
		right: 348px;
		top: 112px;

		@include xs(right, -41px);
		@include xs(top, 318px);
		@include xs(width, 66%);
	}

	.mail__img {
		position: absolute;
		right: 48px;
		top: -102px;

		@include xs(top, -142px);
		@include xs(right, 120px);
		@include xs(width, 66%);
	}

	.mail__form {
		margin-left: 68px;

		@include xs(margin-left, auto);
		@include xs(text-align, center);


		input {
			width: 268px;
			margin-top: 16px;
			padding: 7px 0px 7px 16px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(51, 51, 51, 0.5);

			border: 1px solid #DADADA;
			border-radius: 5px;
			cursor: pointer;

			&:focus,
			&:hover:active {
				outline: none;
				border: 2px solid #24aceb;
			}
		}


		button {
			margin-left: 8px;
			width: 122px;
			height: 48px;
			background: #F7D06F;
			border-radius: 6px;
			cursor: pointer;

			font-family: 'Inter';
			font-style: normal;
			font-weight: bold;
			font-size: 14px;
			line-height: 17px;
			text-align: center;
			text-transform: uppercase;
			color: #56597A;

			&:focus,
			&:hover {
				border: none;
				outline: none;
				opacity: 0.8;
			}
			@include xs(margin-top, 20px);

		}
	}
}

//team

.team {
	position: relative;
	margin-bottom: 200px;
	margin-top: 137px;
	width: 100%;

	@include xs(margin-top, 220px);


	h5 {
		@include blue();
		text-align: center;
	}

	h2 {
		@include black();
		text-align: center;
	}

	p {
		max-width: 470px;
		width: 100%;
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 20px;
		line-height: 32px;
		color: #333333;
		margin: 0 auto;
		margin-top: 29px;
	}
}

.team__slider {
	width: 100%;
}

.slick-dots {
	//border: 1px solid rgb(28, 3, 253);
	height: 100%;
	display: flex;
	justify-content: center;
	margin: 0;
	list-style-type: none;

	@include xs(margin-left, -20px);


	li {
		margin: 0 0.25rem;
	}

	button {
		width: 16px;
		height: 16px;
		background: #74A2D6;
		padding: 0;
		border-radius: 8px;
		text-indent: -9999px;
		cursor: pointer;

		&:focus,
		&:hover {
			border: none;
			outline: none;
		}
	}

	li.slick-active button {
		background: #74A2D6;
		opacity: 0.24;
	}
}




.team__slider-box {
	//border: 1px solid rgb(253, 31, 235);
	margin-top: 35px;
	margin-bottom: 64px;
	max-width: 300px;
	width: 100%;
	height: 336px;
	background: #FFFFFF;

	@include xs(margin-left, 20px);

	&:hover {
		box-shadow: 0px 24px 48px rgba(0, 2, 30, 0.0813519);
		border-radius: 16px;

		@include xs(width, 99%);

		.instagram {
			display: block;
			position: absolute;
			right: 21px;
			bottom: -17px;

			@include xs(right, 40px);
		}
	}

	.instagram {
		display: none;
	}

	.slider__item {
		position: relative;
		padding-top: 32px;
		padding-left: 38px;

		@include xs(margin-left, 20px);

		h6 {
			margin-top: 23px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: bold;
			font-size: 24px;
			line-height: 32px;
			color: #333333;
		}

		p {
			margin-top: 5px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(51, 51, 51, 0.5);
		}
	}
}