@charset "utf-8";
@import "_smart-grid";

.contactForm {
	margin-top: 200px;
	margin-bottom: 190px;
	position: relative;
	width: 100%;
}

.contactForm__img {
	position: absolute;
	top: -40px;
	right: -40px;


	@include xs(top, 480px);
	@include xs(right, -43px);

	@include xsl(top, 520px);
	@include xsl(right, -55px);


	img {}
}

.contactForm__box {
	//border: 1px solid rgb(3, 41, 253);
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);
	

}

.contactForm__box-left {

	h5 {
		font-family: 'Palanquin Dark';
		font-weight: normal;
		font-size: 18px;
		line-height: 32px;
		letter-spacing: 3px;
		text-transform: uppercase;
		color: #EF634D;
	}

	h3 {
		font-family: 'Palanquin Dark';
		font-weight: normal;
		font-size: 56px;
		line-height: 64px;
		color: #333333;


		@include xs(font-size, 35px);
		@include xs(line-height, 44px);


	}
}

.form {
	margin-top: 39px;

	label {
		display: flex;
		flex-direction: column;
		width: 470px;

		@include xs(width, 100%);

		input,textarea {
			margin-top: 16px;
			padding: 8px 0px 8px 16px;
			font-family: 'Palanquin';
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(51, 51, 51, 0.501557);
			border: 1px solid #DADADA;
			border-radius: 5px;
			cursor: pointer;

			&:focus,
			&:hover:active {
				outline: none;
				border: 2px solid #bad80d;
			}
		}

		.form__name {
			//border: 1px solid rgb(3, 41, 253);
		}

		.form__text {
			
			margin-top: 16px;
			padding: 8px 0px 8px 16px;
			font-family: 'Palanquin';
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(51, 51, 51, 0.501557);
			border: 1px solid #DADADA;
			border-radius: 5px;
			cursor: pointer;

			

			&:focus,
			&:hover:active {
				outline: none;
				border: 2px solid #bad80d;
			}
		}

		.form__submit {
			width: 170px;
			height: 48px;
			background: #E13856;
			border-radius: 24px;
			font-family: 'Palanquin Dark';
			font-weight: bold;
			font-size: 18px;
			line-height: 33px;
			color: #FFFFFF;
			padding: 0px;
		}
	}
}

.contactForm__box-right {


	@include xs(margin-top, 50px);

	@include xsl(margin-top, 90px);

	
	.contactForm__map {
		position: relative;
		width: 570px;
		height: 496px;
		border: 8px solid #E13856;
		border-radius: 23px;



		@include xs(width, 100%);
		@include xs(height, 300px);
		@include xs(border, 5px solid #E13856);

		@include xsl(width, 96%);


		#map {
			width: 100%;
			height: 100%;
			border-radius: 23px;
		}
	}
}