@charset "utf-8";
@import "_smart-grid";

.about {
	margin-top: 176px;
	width: 100%;
}

.about__box {
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);
}

.about__left{

	img{
		
		@include xs(width, 100%);
	}
}

.about__right {
	max-width: 470px;

	@include xs(margin-top, 100px);


	.about__right-h5 {
		@include blue();
	}

	.about__right-h2 {
		@include black();
		margin-top: 20px;

		@include xs(font-size, 50px);

	}

	.about__right-strong {
		margin-top: 20px;

		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 20px;
		line-height: 32px;
		color: #333333;
	}

	p {
		margin-top: 20px;
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 32px;
		color: rgba(51, 51, 51, 0.5);
	}

	button {
		margin-top: 30px;
		border: 1px solid rgba(116, 162, 214, 0.503736);
		box-sizing: border-box;
		border-radius: 6px;
		width: 138px;
		height: 48px;
		cursor: pointer;

		font-family: 'Inter';
		font-style: normal;
		font-weight: bold;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		text-transform: uppercase;
		color: #74A2D6;

		&:focus,
		&:hover {
			outline: none;
			opacity: 0.8;
		}

	}
}



.search {
	margin-top: 200px;
	width: 100%;
}

.search__top {
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);
}

.search__top-left {

	h5 {
		@include blue();
	}

	h2 {
		@include black();
		margin-top: 20px;

	}
}

.search__top-right {
	max-width: 470px;

	p {
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 20px;
		line-height: 32px;
		color: #333333;

	}
}

.search__bottom {
	margin-top: 67px;
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);

	.search__bottom-item {
		//border: 1px solid green;
		width: 368px;
		margin-bottom: 100px;

		@include xs(width, 100%);

		img {
			padding-left: 38px;
			padding-top: 38px;
		}

		h5 {
			padding-left: 38px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: bold;
			font-size: 24px;
			line-height: 32px;
			color: #333333;

			@include xs(margin-top, 100px);

		}

		p {
			padding-left: 38px;
			padding-right: 24px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(51, 51, 51, 0.5);
		}

		.p__black {
			padding-left: 38px;
			padding-bottom: 38px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: #333333;
		}

		&:hover{
			width: 368px;
			background: #FFFFFF;
			box-shadow: 0px 24px 48px rgba(0, 2, 30, 0.0813519);
			border-radius: 16px;
			
			@include xs(width, 100%);

		}
	}
}