@charset "utf-8";
@import "_smart-grid";

.section__bottom {
	position: relative;
	margin: 0 auto;
	max-width: 1440px;
	width: 100%;
	min-height: 2288px;
	height: 100%;
	background: #56597A;
}

.section__bottom-top {
	position: relative;
	margin-top: 106px;
	width: 100%;
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);
}

.left {

	h5 {
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 32px;
		letter-spacing: 3px;
		text-transform: uppercase;
		color: #F7D06F;
	}

	h2 {
		margin-top: 10px;
		font-family: 'Inter';
		font-style: normal;
		font-weight: bold;
		font-size: 56px;
		line-height: 64px;
		color: #FFFFFF;
	}
}

.right {
	position: absolute;
	right: 0;
	bottom: 5px;

	button {
		width: 150px;
		height: 48px;
		border: 1px solid rgba(255, 255, 255, 0.295743);
		box-sizing: border-box;
		border-radius: 6px;
		background: #56597A;


		font-family: 'Inter';
		font-style: normal;
		font-weight: bold;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF;
		cursor: pointer;

		&:focus,
		&:hover {
			outline: none;
			opacity: 0.9;
			color: #74A2D6;
			border: 1px solid #74A2D6;
		}
	}
}

.section__bottom-bottom {
	width: 100%;
	margin-top: 67px;
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);


	.box {
		max-width: 373px;
		width: 100%;

		img {

			@include xs(width, 100%);
		}

		.p__top {
			margin-top: 26px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: rgba(255, 255, 255, 0.502972);
		}

		.p__center {
			margin-top: 13px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: bold;
			font-size: 24px;
			line-height: 32px;
			color: #FFFFFF;
		}

		.a__bottom {
			margin-top: 19px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 32px;
			color: #FFFFFF;

			&:hover {
				color: #74A2D6;
			}
		}
	}
}



//Form
.section__form {}

.section__bottom-img {
	position: absolute;
	width: 100%;
	top: 1088px;

	@include xs(display, none);

}

.section__form-box {
	margin: 0 auto;

	.telegram {
		position: absolute;
		top: 197px;
		right: -87px;

		@include xs(top, -60px);
		@include xs(right, -25px);

	}

	.icon-form {
		position: absolute;
		top: 384px;
		left: -100px;

		@include xs(display, none);

	}

	.form__box {
		position: relative;
		margin-top: 182px;

		width: 979px;
		z-index: 12;
		height: 784px;
		background: #FFFFFF;
		border-radius: 24px;

		@include xs(width, 100%);
		@include xs(height, 100%);



		h5 {
			padding-top: 57px;
			@include blue();
			text-align: center;
		}

		h2 {
			padding-top: 19px;
			@include black();
			text-align: center;

			@include xs(font-size, 42px);
		}

		p {
			margin: 0 auto;
			padding-top: 29px;
			width: 470px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: normal;
			font-size: 20px;
			line-height: 32px;
			text-align: center;
			color: #333333;

			@include xs(width, 100%);


		}
	}
}

.form {
	//border: 1px solid rgba(116, 162, 214, 0.503736);

	margin-top: 60px;
	margin-left: 101px;
	margin-right: 101px;
	font-family: 'Inter';
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 32px;
	color: rgba(51, 51, 51, 0.5);


	@include xs(width, 100%);
	@include xs(margin-left, 0px);
	@include xs(margin-right, 0px);




	.form__top {
		//border: 1px solid rgba(116, 162, 214, 0.503736);
		width: 100%;


		@include xs(width, 90%);
		@include xs(margin, 0 auto);

		.form__top-right {
			//border: 1px solid rgba(8, 103, 245, 0.504);
			margin-top: 32px;
			display: flex;
			justify-content: space-between;

			@include xs(flex-direction, column);
			@include xs(margin-top, 0px);


			input {
				width: 378px;
				padding: 9px 0px 7px 16px;
				background: #FFFFFF;
				border: 1px solid #DADADA;
				box-sizing: border-box;
				border-radius: 6px;

				@include xs(width, 100%);
				@include xs(margin-top, 20px);


			}
		}

		.form__top-left {
			//border: 1px solid rgba(255, 60, 0, 0.504);
			display: flex;
			justify-content: space-between;

			@include xs(flex-direction, column);


			input {
				width: 378px;
				padding: 11px 0px 11px 16px;
				background: #FFFFFF;
				border: 1px solid #DADADA;
				box-sizing: border-box;
				border-radius: 6px;

				@include xs(width, 100%);
				@include xs(margin-top, 20px);
			}
		}
	}

	.form__textarea {
		border: 1px solid rgba(255, 60, 0, 0.504);
		margin-top: 32px;
		max-width: 777px;
		width: 100%;
		padding: 11px 0px 11px 16px;
		background: #FFFFFF;
		border: 1px solid #DADADA;
		box-sizing: border-box;
		border-radius: 6px;

		@include xs(margin-top, 20px);
		@include xs(max-width, 300px);
		@include xs(width, 100%);
	}

	.form__bottom {

		width: 100%;
		text-align: center;

		button {
			margin-top: 32px;
			width: 171px;
			height: 48px;
			background: #EF86B0;
			border-radius: 6px;
			font-family: 'Inter';
			font-style: normal;
			font-weight: bold;
			font-size: 14px;
			line-height: 17px;
			text-align: center;
			text-transform: uppercase;
			color: #FFFFFF;
			cursor: pointer;

			@include xs(margin-top, 20px);
			@include xs(margin-bottom, 40px);

			&:focus,
			&:hover {
				outline: none;
				opacity: 0.9;

			}
		}
	}
}


.footer {
