	}
}

hr {
	margin-top: 50px;
	background: #FFFFFF;
	opacity: 0.3;
}

.footer__bottom {
	margin-top: 41px;
	display: flex;
	justify-content: space-between;

	@include xs(flex-direction, column);
	@include xs(margin-bottom, 50px);

}

.footer__bottom-left {

	p {

		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 32px;
		color: rgba(255, 255, 255, 0.502972);

		@include xs(text-align, center);
