@charset "utf-8";
@import "_smart-grid";
@import "settings/_settings";
@import "fonts/_fonts";

.header {
	background-image: url('../img/bg.png');
	background-repeat: no-repeat;
	background-size: cover;
	width: 100%;
	max-width: 1440px;
	margin: 0 auto;
	height: 880px;
}


.header__box {
	//border: 1px solid black;
	margin-top: 20px;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;

	.header__logo {

		img {}
	}

	.header__menu {
		@include xs(display, none);


		ul {

				@include xs(flex-direction, column);
				@include xs(position, absolute);
				@include xs(z-index, 2);
				@include xs(top, 50px);
				@include xs(left, 0px);
				@include xs(right, 0px);
				@include xs(background,  #74A2D6);
				@include xs(padding-top, 30px);
				@include xs(padding-bottom, 30px);

			li {
				list-style-type: none;
				display: inline-block;

				@include xs(display, flex);
				@include xs(justify-content, center);
				@include xs(margin-left, -40px);

				a {
					font-family: 'Inter';
					font-style: normal;
					font-weight: normal;
					font-size: 16px;
					line-height: 32px;
					text-align: right;
					color: #FFFFFF;
					margin-left: 23px;

					&:hover {
						transition: 1s;
						font-size: 20px;
						color: #EF86B0;

						@include xs(font-size, 35px);
					}

					@include xs(font-size, 30px);
				}
			}
		}
	}
}

.header__menu-burger {
	display: none;

	@include xs(display, block);
}


.header__content {
	display: flex;
	justify-content: center;
	margin-top: 113px;

	@include xs(flex-direction, column);
}

.header__content-left {

	@include xs(order, 2);

	h4 {
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 32px;
		letter-spacing: 3px;
		text-transform: uppercase;
		color: #F7D06F;
	}

	h1 {
		margin-top: 20px;
		font-family: 'Inter';
		font-style: normal;
		font-weight: bold;
		font-size: 68px;
		line-height: 86px;
		color: #FFFFFF;

		@include xs(font-size, 58px);
		@include xs(line-height, 73px);

	}

	p {
		margin-top: 20px;
		font-family: 'Inter';
		font-style: normal;
		font-weight: normal;
		font-size: 24px;
		line-height: 38px;
		color: #FFFFFF;
	}

	.header__button {
		margin-top: 80px;
		display: flex;
		align-items: center;

		button {
			width: 201px;
			height: 53px;
			background: #EF86B0;
			border-radius: 6px;

			font-family: 'Inter';
			font-style: normal;
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
			text-align: center;
			text-transform: uppercase;
			border: none;
			color: #FFFFFF;
			cursor: pointer;

			&:focus,
			&:hover {
				outline: none;
				background: #d67ca0;
			}
		}

		.header__button-video {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-left: 22px;

			img {
				height: 54px;
			}

			p {
				height: 54px;
				font-family: 'Inter';
				font-style: normal;
				font-weight: normal;
				font-size: 16px;
				line-height: 32px;
				color: #FFFFFF;
				margin-left: 21px;
			}
		}
	}
}

.header__content-right {
	margin-top: -70px;

	img {
		margin-right: -32px;

		@include xs(width, 100%);
		@include xs(margin-right, 0);

		@include xsl(width, 92%);
	}
	@include xs(order, 1);
}