@charset "utf-8";
@import "_smart-grid";


@mixin blue {
	font-family: 'Inter';
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 32px;
	letter-spacing: 3px;
	text-transform: uppercase;
	color: #74A2D6;
}




@mixin black {
	font-family: 'Inter';
	font-style: normal;
	font-weight: bold;
	font-size: 56px;
	line-height: 64px;
	color: #333333;

}